export default {
    'url': '/terminalTrend/map/province',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'province|2-5': [
                {
                    'name|+1': ['', '','','', ''],
                    'value|30-100': 70,
                    'detail|4': [
                        {
                            'name|+1': ['','U','',''],
                            'value|100-9999': 80
                        }
                    ]
                }    
            ]
        }
    }
}


