export default {
    'url': '/terminalTrend/map/mainCity',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'mainCity|1-2': [
                {
                    'name|+1': ['', ''],
                    'value|30-100': 70,
                    'detail|4': [
                        {
                            'name|+1': ['','U','',''],
                            'value|100-9999': 80
                        }
                    ]
                }    
            ]
        }
    }
  }


